<template>
    <div>
        <Form ref="formModel" :model="formModel" :label-width="120">
            <div>
                <h3 class="margin-b-14">企业担保人
                </h3>
                <Row>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="与申请人关系">
                            <span class="spanline32">{{formModel.guarantorRelationshipName}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="企业名称">
                            <span class="spanline32">{{formModel.enterpriseName}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="企业性质">
                            <span class="spanline32">{{formModel.enterpriseNatureName}}</span>
                        </FormItem>
                    </i-col>

                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="统一社会信用代码">
                            <span class="spanline32">{{formModel.unifiedSocialCreditCode}}</span>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="法人姓名">
                            <span class="spanline32">{{formModel.corporationName}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="法人证件号码">
                            <span class="spanline32">{{formModel.corporationIdentityCard}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="法人联系电话">
                            <span class="spanline32">{{formModel.corporationPhone}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="实际控制人姓名">
                            <span class="spanline32">{{formModel.actualControllerName}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="实际控制人证件号码">
                            <span class="spanline32">{{formModel.actualControllerIdentityCard}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="实际控制人联系电话">
                            <span class="spanline32">{{formModel.actualControllerPhone}}</span>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="8">
                        <FormItem label="企业注册地">
                            <span class="spanline32">{{formModel.enterpriseRegistrationProvinceName}}{{!!formModel.enterpriseRegistrationCityName?'/':''}}{{formModel.enterpriseRegistrationCityName}}{{!!formModel.enterpriseRegistrationDistrictName?'/':''}}{{formModel.enterpriseRegistrationDistrictName}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="企业注册地详细地址">
                            <span class="spanline32">{{formModel.enterpriseRegistrationAddress}}</span>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="8">
                        <FormItem label="企业经营地">
                            <span class="spanline32">{{formModel.enterpriseManagementProvinceName}}{{!!formModel.enterpriseManagementCityName?'/':''}}{{formModel.enterpriseManagementCityName}}{{!!formModel.enterpriseManagementDistrictName?'/':''}}{{formModel.enterpriseManagementDistrictName}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="企业经营地详细地址">
                            <span class="spanline32">{{formModel.enterpriseManagementAddress}}</span>
                        </FormItem>
                    </i-col>
                </Row>
            </div>
        </Form>
    </div>
</template>
<script>
    /**
     * @Description: 企业担保人表单
     * @Props: {index: Number} 当前担保人在进件数据中的索引值
     */
    import base from './js/custbase';

    export default {
        name: 'orderGuarantorEnterpriseForm',
        mixins: [base],
        props: {
            index: {
                default: 0
            },
        },
        components: {},
        data() {
            return {}
        },
        computed: {
            //返回表单信息
            formModel() {
                let obj = this.orderData.cust.guarantorEnterprise[this.index];
                if (!obj) return undefined;
                return obj;
            },
        },
        mounted() {
            this.$nextTick(() => {
                this.todoRecount();
            })
        },
        methods: {},
    }
</script>



