<template>
    <div class="cm-panel" style="min-height:500px">
        <ul class="readInfo" v-if="!loglistdata || !loglistdata.length">
            <li>
                <div class="text-center withicon-empty-tip">
                    <img :src="emptyTipImg" alt="" height="200" style="margin-top:100px"><br>
                    <span style="font-size:18px">暂无数据</span>
                </div>
            </li>
        </ul>
        <ul class="readInfo" v-else>
            <li v-for="(item, index) in loglistdata">
                <div class="statuscon">
                    <Icon :type="iconType(item)" :class="iconClass(item)"></Icon>
                    <span class="status">状态：{{item.orderStatusDesc}}</span><span
                        class="times">{{item.operatorTime|datetime}}</span>
                </div>
                <p class="orderInfo">审批结果：<span :class="iconClass(item)">{{item.approveDesc}}</span></p>
                <p class="orderInfo">审批意见描述：{{item.approveComments}}</p>
                <p class="orderInfo">审批备注：{{item.approve}}</p>
                <p class="orderInfo">操作人：{{item.actualOperator}}</p>
            </li>
        </ul>
    </div>
</template>

<script>
    /**
     * @Description: 审批结果标签页
     */
    export default {
        name: "approvalResult",
        props: ['loglistdata'],
        data() {
            return {
                emptyTipImg: require('@/assets/img/empty_tip.png')
            }
        },
        mounted() {
        },
        methods: {
            //设置不同的icon
            iconType(obj) {
                let orderStatus = obj.approveOpinion;
                /*
                agree  --通过  蓝色
                cancel --取消 red
                fetchback --提回  yellow
                refuse --拒绝  red
                reject --退回    yellow
                taskturns --转件  蓝色
                other  --其他  蓝色
                */
                const redArr = ["cancel", "refuse"]; // 红色
                const yellowArr = ["fetchback", "reject"]; // 黄色
                return (redArr.indexOf(orderStatus) >= 0) ? "md-close-circle" : (yellowArr.indexOf(orderStatus) >= 0) ? "md-alert" : "md-checkmark-circle";
            },
            //icon设置不同的颜色
            iconClass(obj) {
                let orderStatus = obj.approveOpinion;
                const redArr = ["cancel", "refuse"]; // 红色
                const yellowArr = ["fetchback", "reject"]; // 黄色
                return (redArr.indexOf(orderStatus) >= 0) ? 'iconredStyle' : (yellowArr.indexOf(orderStatus) >= 0) ? 'iconyellowStyle' : 'iconStyle';
            },
        }
    }
</script>




