<template>
    <div>
        <div class="cm-panel" v-if="contactorArr && contactorArr.length">
            <h3 class="margin-b-14">联系人信息</h3>
            <div v-for="(item, index) in contactorArr" :key="index">
                <Form :ref="'contactor' + index" :model="item" :label-width="120">
                    <Row>
                        <i-col :xs="24" :sm="12" :md="8">
                            <FormItem label="与申请人关系">
                                <span class="spanline32">{{item.contactRelationshipName}}</span>
                            </FormItem>
                        </i-col>
                        <i-col :xs="24" :sm="12" :md="8">
                            <FormItem label="姓名">
                                <span class="spanline32">{{item.contactName}}</span>
                            </FormItem>
                        </i-col>
                        <i-col :xs="24" :sm="12" :md="8">
                            <FormItem label="电话">
                                <span class="spanline32">{{item.contactPhone}}</span>
                            </FormItem>
                        </i-col>
                    </Row>
                </Form>
            </div>
        </div>
    </div>
</template>
<script>
    /**
     * @Description: 联系人表单
     */
    import base from './js/custbase';

    export default {
        name: 'orderContactorForm',
        mixins: [base],
        props: {},
        components: {},
        data() {
            return {}
        },
        computed: {
            //返回联系人数组
            contactorArr() {
                return this.orderData.cust.contactor;
            },
        },
        mounted() {
            this.$nextTick(() => {
                this.todoRecount();
            })
        },
        methods: {},
    }
</script>



