<template>
    <div>
        <h3 class="margin-b-14">亲属信息</h3>
        <Form ref="formModel" :model="formModel" :label-width="120">
            <Row>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="与申请人关系">
                        <span class="spanline32">{{formModel.relativeTypeName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="姓名">
                        <span class="spanline32">{{formModel.relativeName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="证件类型">
                        <span class="spanline32">{{formModel.relativeCertificateTypeName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="证件号码">
                        <span class="spanline32">{{formModel.relativeCertificateNo}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="手机号">
                        <span class="spanline32">{{formModel.relativePhone}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="税后年薪（万元）">
                        <span class="spanline32">{{formModel.relativeAnnualSalaryAfterTax|amount}}</span>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24">
                    <FormItem label="单位名称">
                        <span class="spanline32">{{formModel.relativeCompanyName}}</span>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="8">
                    <FormItem label="单位所在地省市区">
                        <span class="spanline32">{{formModel.relativeCompanyProvinceName}}{{!!formModel.relativeCompanyCityName?'/':''}}{{formModel.relativeCompanyCityName}}{{!!formModel.relativeCompanyDistrictName?'/':''}}{{formModel.relativeCompanyDistrictName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="16">
                    <FormItem label="单位详细地址">
                        <span class="spanline32">{{formModel.relativeCompanyAddress}}</span>
                    </FormItem>
                </i-col>
            </Row>
        </Form>
    </div>
</template>
<script>
    /**
     * @Description: 个人申请人的亲属表单
     */
    import {mapState, mapGetters, mapMutations, mapActions} from 'vuex'
    import base from './js/custbase';

    export default {
        name: 'orderRelativeForm',
        mixins: [base],
        props: {},
        components: {},
        data() {
            return {
                shipDisabled: false,
            }
        },
        computed: {
            //返回亲属model
            formModel() {
                return this.orderData.cust.relative;
            },
            personalInfo() {
                return this.orderData.cust.personal;
            },
        },
        mounted() {
            this.$nextTick(() => {
                this.todoRecount();
            })
        },
        methods: {},
    }
</script>



