<template>
    <div class="margin-tb-10 margin-lr-20 layout-content form-readonly" style="padding-bottom:60px">
        <!--顶部状态步骤栏-->
        <div class="margin-b-10">
            <apply-steps :active-step="orderStep"></apply-steps>
        </div>

        <!--订单基本信息栏-->
        <div class="cm-panel">
            <div class="order-info-row flex-box">
                <ul style="flex:1" class="padding-r-20">
                    <li><span class="order-info-label">订单号：</span><span
                            class="order-info-value">{{orderinfo.orderNo}}</span></li>
                    <li v-if=" !isQy || (isQy && !!orderinfo.customerName)"><span
                            class="order-info-label">{{isQy? '企业名称':'个人'}}：</span><span class="order-info-value">{{orderinfo.customerName}}</span>
                    </li>
                    <li v-if="!isQy"><span class="order-info-label">手机号码：</span><span class="order-info-value">{{orderinfo.customerPhone}}</span>
                    </li>
                    <li><span class="order-info-label">产品方案：</span><span class="order-info-value">{{orderinfo.productName}}</span>
                    </li>
                    <li><span class="order-info-label">主体：</span><span
                            class="order-info-value">{{orderinfo.mainSubjectDesc}}</span></li>
                    <li><span class="order-info-label">渠道：</span><span
                            class="order-info-value">{{orderinfo.dealerName}}</span></li>
                    <li><span class="order-info-label">意向车型：</span><span class="order-info-value">{{orderinfo.fullCarStyleText}}</span>
                    </li>
                </ul>
            </div>
        </div>

        <!--标签栏-->
        <div id="tabBar" :class="tabBarFixed?'fixed-tab-bar':''">
            <div class="cm-panel">
                <div class="flex-box">
                    <div class="flex-box">
                        <!--:class="$route.name===item.routeName?'active':''"-->
                        <div class="applytab-bar-cell" :class="activeStep===index?'active':''"
                             v-for="(item,index) in orderTabs" :key="index"
                             @click="jump(index)">
                            <!--<Tag type="dot" :color="activeStep===index ? 'blue': ''" @click.native="jump(index)">{{item.title}}</Tag>-->
                            <div class="applytab-bar-cell-title">{{item.title}}</div>
                            <!--进度条-->
                            <div class="ivu-progress ivu-progress-success ivu-progress-show-info" v-if="index<2">
                                <div class="ivu-progress-outer">
                                    <div class="ivu-progress-inner">
                                        <div class="ivu-progress-bg" style="height: 10px;"
                                             :style="progressStyle(item,index)"></div>
                                    </div>
                                </div>
                                <span class="ivu-progress-text" :style="progressTextColor(item,index)">{{item.count}}/{{item.total}}                        </span>
                            </div>
                        </div>
                    </div>
                    <div style="flex: 1;" class="text-right font0" v-if="loaded">
                        <Button @click="$router.back()" custom-icon="iconfont icon-back"
                                style="color:#999">返回
                        </Button>
                    </div>
                </div>
            </div>
        </div>

        <!--子组件-->
        <div>
            <apply-client-module ref="applyClientModule" v-if="loadCustModule && orderData.orderinfo"
                                 v-show="!activeStep"></apply-client-module>

            <apply-loan-module ref="applyLoanModule" v-if="loadLoanModule && orderData.orderinfo"
                               v-show="activeStep==1"></apply-loan-module>

            <div class="cm-panel" v-show="activeStep===2">
                <h3>附件信息</h3>
                <iframe :src="mcloudSrc" ref="mobsf" scrolling="no" frameborder="0" style="width:100%;height:500px;"
                        @load="hideLoading()"></iframe>
            </div>
            <!--审批结果-->
            <approval-result ref="approvalResult" v-show="activeStep===3" :loglistdata="loglistdata"
                             v-if='approvalResultModule'></approval-result>

        </div>

        <!--面包屑-->
        <div class="simple-breadcrumb col-xs-11">
            <router-link to="/order/list">进件管理</router-link>
            <Icon type="ios-arrow-forward"></Icon>
            <span>进件提报</span>
        </div>
    </div>
</template>

<script>
    /**
     * @Description: 订单查看页
     */
    import {mapState, mapGetters, mapMutations, mapActions} from 'vuex'

    import applySteps from "@/components/apply/applySteps.vue";
    import applyLoanModule from "./loan";
    import applyClientModule from "./cust";
    import approvalResult from "./approvalResult";

    import {yx, scrollTo} from "@/utils/helper";

    export default {
        name: "orderView",
        components: {
            applySteps,
            applyLoanModule,
            applyClientModule,
            approvalResult
        },
        data() {
            return {
                loglistdata: [],
                orderNo: this.$route.params.orderNo,
                account: this.$store.state.app.userInfo.userAccount,
                userName: this.$store.state.app.userInfo.userName,
                orderStep: 0,//顶部步骤条所在节点，对应12345
                loaded: false, //页面数据是否加载成功
                loadCustModule: false, //加载客户模块
                loadLoanModule: false, //加载贷款模块
                loadAttachModule: false, //加载附件模块
                approvalResultModule: false,
                mcloudSrc: '',
                showSubmitConfirmModal: false, //显示提交前确认弹窗
                activeStep: 0,
                custType: "1",
                showRepayModal: false,
                handleContactorEditIndex: undefined,
                handleBondsmanEditIndex: undefined,
                zfResultArrived: false,//接收到资方准入结果
                zfTimer: undefined,//资方准入请求超时定时器
                tableData: {
                    repayTbl: {
                        showModal: false,
                    },
                    zifang: {
                        title: '资方准入结果',
                        showModal: false,
                        loading: false,
                        total: 0,
                        data: [{
                            name: '民生银行',
                            result: false,
                            errors: [{
                                index: 1,
                                rule: '客户年龄不符'
                            }, {
                                index: 2,
                                rule: '命中保密规则'
                            }]
                        },
                            {
                                name: '微众银行',
                                result: true,
                            }
                        ],
                        columns: [{
                            title: "序号",
                            width: 70,
                            align: 'left',
                            render: (h, params) => {
                                return params.index + 1;
                            }
                        }, {
                            title: '资方名称',
                            key: 'name',
                        },
                            {
                                title: '准入结果',
                                key: 'result',
                                render: (h, params) => {
                                    if (params.row.result == true)
                                        return h('span', {
                                            'class': 'color-primary'
                                        }, '通过');
                                    if (params.row.errors) {
                                        return h('div', params.row.errors.map((item) => {
                                            return h('div', {}, item.index + '、' + item.rule)
                                        }))
                                    }
                                }
                            },
                        ]
                    },
                    attachment: {
                        title: '附件信息',
                        formName: 'attachmentModel',
                        showModal: false,
                        exportLoading: false,
                        current: 1,
                        pageSize: 10,
                        loading: false,
                        total: 0,
                        data: [],
                        columns: [{
                            type: "selection",
                            title: '#'
                        }, {
                            title: '大类名称',
                            key: 'category',
                        },
                            {
                                title: '小类名称',
                                key: 'attachId',
                            },
                            {
                                title: '上传状态',
                                key: 'status',
                            },
                            {
                                title: '操作',
                                key: 'action',
                                width: 120,
                                align: 'center',
                                render: (h, params) => {
                                    return h('div', {
                                        props: {},
                                        'class': 'none-padding',
                                        slot: 'content'
                                    }, [
                                        h('a', {
                                            props: {
                                                type: 'primary',
                                                size: 'small',
                                            },
                                            style: {
                                                display: 'block'
                                            },
                                            on: {
                                                click: () => {
                                                    this.edit('attachment', params.index)
                                                }
                                            }
                                        }, '编辑'),
                                        h('a', {
                                            props: {
                                                type: 'error',
                                                size: 'small',
                                            },
                                            style: {
                                                display: 'block'
                                            },
                                            on: {
                                                click: () => {
                                                    this.remove('attachment', params.index)
                                                }
                                            }
                                        }, '删除')
                                    ])
                                }
                            }
                        ]
                    },

                },
                loading: false,
                exportLoading: false,
                pager: {
                    "total": 0,
                    "Opts": [10, 20, 50, 100, 200]
                },
                dateGroup: [],
                saveT: undefined,
                tempSaving: false,
                tabBarFixed: false,
                zfPassNum: 0,
                zfRefuseNum: 0,
                zfResultList: [],
                detailInfo: {},//详情订单信息
                remark: '',//备注
            }
        },
        computed: {
            ...mapState('applyview', [
                'orderData',
                'orderTabs'
            ]),
            ...mapGetters('applyview', [
                'getFieldsCount',
                'getFieldsTotal',
                'todoSaveCache',
            ]),
            orderinfo() {
                return this.orderData.orderinfo || {};
            },
            isQy() {
                return yx.isQy(this.$route.params.customerType);
            },
            //因进度条组件无法同时绑定进度条和字体颜色 ，所以改成直接使用元素渲染
            //获取进度条百分比及背景色
            progressStyle() {
                return (item, index) => {
                    return {
                        'width': this.getProgressPct(item, index) + '%',
                        'background-color': this.getProgressClass(item, index) == 'success' ? '#1FA269' :
                            this.getProgressClass(item, index) == 'wrong' ? '#F5222D' : '#999999'
                    }
                }
            },
            //进度条颜色控制
            progressTextColor() {
                return (item, index) => {
                    return {
                        color: this.getProgressClass(item, index) == 'success' ? '#1FA269' :
                            this.getProgressClass(item, index) == 'wrong' ? '#F5222D' : '#999999'
                    }
                }
            }
        },
        mounted() {
            document.title = '进件提报';

            //初始查询进件信息
            this.queryInit();
            //监听页面滚动事件
            window.addEventListener('scroll', this.handleScroll);
        },
        //离开当前页面后执行
        destroyed() {
            window.removeEventListener('scroll', this.handleScroll); //页面销毁时取消页面滚动监听事件
            if (window.saveT) clearInterval(window.saveT)
        },
        methods: {
            ...mapMutations('applyview', [
                'setOrderInfo'
            ]),
            ...mapMutations('app', [
                'showLoading',
                'hideLoading',
            ]),
            //封装报错弹窗
            errorModal(content, onOk) {
                let options = {
                    title: '提示',
                    content: content,
                };
                if (onOk) options.onOk = onOk;
                this.$Modal.error(options);
            },
            //进度条的样式
            getProgressClass(item, index) {
                let fill = item.count > 0;//只要填写了就有颜色
                return item.hasError ? 'wrong' : (fill ? 'success' : 'normal');
            },

            //进度条的百分比
            getProgressPct(item, index) {
                return (item.count * 100) / item.total;
            },
            //标签页吸顶
            handleScroll() {
                var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
                var offsetTop = document.querySelector('#tabBar').offsetTop;
                this.tabBarFixed = scrollTop > offsetTop;
            },
            //初始化数据查询
            async queryInit() {
                let that = this;
                //订单状态
                let order = await this.queryOrderStatusInfo();
                order.orderNo = this.$route.params.orderNo;
                const steps = ['level11', 'level12', 'level13', 'level14'];
                let stepIndex = steps.indexOf(order.orderSubStage);
                this.orderStep = stepIndex > -1 ? stepIndex + 1 : 1;//设置顶部节点
                this.setOrderInfo(order);
                this.loadCustModule = true;
                //加载贷款模块
                this.loadLoanModule = true;
                this.approvalResultModule = true;
                this.orderFlowLog();
                //数据请求结束
                this.loaded = true;
            },
            //查询订单状态节点及预审等基本信息
            async queryOrderStatusInfo() {
                let url = polarisserver + '/credit/getOrderModifyDetail/' + this.account + '/' + this.orderNo;
                this.showLoading();
                return new Promise((resolve, reject) => {
                    this.$http.get(url).then((res) => {
                        this.hideLoading();
                        let result = res.data;
                        if (!result.success) return this.errorModal(result.errorMessage || '订单状态 请求返回格式不对');
                        if (!result.data) return this.errorModal('没有该订单状态信息');
                        resolve(result.data);
                    }).catch((err) => {
                        this.hideLoading();
                        this.errorCatchModal('请求订单状态出错：', err)
                    })
                })
            },
            //请求云盘页面地址
            queryMcoudSrc() {
                this.showLoading();
                let params = {
                    "orderId": this.$route.params.orderNo,
                    "viewCode": "yixin_xiaofei_polaris_detail",//查看时的视图
                    "orderUserId": this.account,
                    "orderUserName": this.userName
                }
                this.$http.post(baseserver + '/annexStorage/annexcloudview', params).then((res) => {
                    this.hideLoading();
                    let result = res.data;
                    if (!result.success) {
                        return this.$Message.error(result.errorMessage || '请求云盘链接地址失败');
                    }
                    this.mcloudSrc = result.data.visitURL;
                }).catch((error) => {
                    this.hideLoading();
                    this.$Message.error(error || '请求云盘链接地址出错');
                })
            },
            orderFlowLog() {
                let orderNo = this.$route.params.orderNo;
                this.showLoading();
                this.$http.get(polarisserver + '/order/orderFlowLog/' + orderNo, {}).then((res) => {
                    this.hideLoading();
                    let result = res.data;
                    if (!result.success) return this.$Message.error(result.errorMessage || '审批记录请求失败');
                    this.loglistdata = result.data || [];
                }).catch((err) => {
                    this.hideLoading();
                    this.errorCatchModal('审批记录请求出错：', err);
                })
            },
            jump(index) {
                if (!this.orderData.orderinfo) return false;
                this.activeStep = index;
                //滚定到位置tabBar
                let el = $('#tabBar')[0];
                if (el) {
                    var preEl = $('#tabBar').prev();
                    var topEl = preEl.prev();
                    var elsH = preEl.outerHeight(true) + topEl.outerHeight(true) + 10;
                    var barH = $('#tabBar').outerHeight();
                    let st = document.documentElement.scrollTop || document.body.scrollTop;
                    //滚定到位置，固定时滚到顶部减掉固定的bar高，未固定时滚到之前位置
                    let total = this.tabBarFixed ? elsH - barH : st;
                    scrollTo(document, total, 300);
                }
                if (index === 1) {
                    //加载贷款模块
                    this.loadLoanModule = true;
                }

                if (index === 2 && !this.mcloudSrc) {
                    this.queryMcoudSrc();
                }
            }
        }
    }
</script>



