<template>
    <div>
        <Form ref="formModel" :model="formModel" :label-width="120">
            <div>
                <h3 class="margin-b-14">个人担保人</h3>
                <Row>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="与申请人关系">
                            <span class="spanline32">{{formModel.guarantorRelationshipName}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="担保人姓名">
                            <span class="spanline32">{{formModel.guarantorName}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="手机号码">
                            <span class="spanline32">{{formModel.guarantorPhone}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="证件类型">
                            <span class="spanline32">{{formModel.guarantorCredentialTypeName}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="证件号码">
                            <span class="spanline32">{{formModel.guarantorIdentityCard}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="单位电话">
                            <span class="spanline32">{{formModel.companyPhone}}</span>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24">
                        <FormItem label="单位名称">
                            <span class="spanline32">{{formModel.companyName}}</span>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="8">
                        <FormItem label="单位所在地">
                            <span class="spanline32">{{formModel.companyProvinceName}}{{!!formModel.companyCityName?'/':''}}{{formModel.companyCityName}}{{!!formModel.companyDistrictName?'/':''}}{{formModel.companyDistrictName}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="单位详细地址">
                            <span class="spanline32">{{formModel.companyDetailAddress}}</span>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="8">
                        <FormItem label="现居地">
                            <span class="spanline32">{{formModel.currentResidenceProvinceName}}{{!!formModel.currentResidenceCityName?'/':''}}{{formModel.currentResidenceCityName}}{{!!formModel.currentResidenceDistrictName?'/':''}}{{formModel.currentResidenceDistrictName}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="现居地详细地址">
                            <span class="spanline32">{{formModel.currentResidenceAddress}}</span>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="8">
                        <FormItem label="户籍地">
                            <span class="spanline32">{{formModel.censusRegisterProvinceName}}{{!!formModel.censusRegisterCityName?'/':''}}{{formModel.censusRegisterCityName}}{{!!formModel.censusRegisterDistrictName?'/':''}}{{formModel.censusRegisterDistrictName}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="户籍地详细地址">
                            <span class="spanline32">{{formModel.censusRegisterAddress}}</span>
                        </FormItem>
                    </i-col>
                </Row>
            </div>
        </Form>
    </div>
</template>
<script>
    /**
     * @Description: 个人担保人表单
     * @Props: {index: Number} 当前担保人在进件数据中的索引值
     */
    import base from './js/custbase';

    export default {
        name: 'orderGuarantorPersonalForm',
        mixins: [base],
        props: {
            index: {
                default: 0
            },
        },
        components: {},
        data() {
            return {}
        },
        computed: {
            //返回表单信息
            formModel() {
                let obj = this.orderData.cust.guarantorPersonal[this.index];
                if (!obj) return undefined;
                return obj;
            },
        },
        mounted() {
            this.$nextTick(() => {
                this.todoRecount();
            })
        },
        methods: {},
    }
</script>



